.song {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 55vh;

  .song-img {
    width: 15rem;
    height: 15rem;
  }

  img {
    width: 15rem;
    height: 15rem;
    object-fit: cover;
    border-radius: 50%;
  }
  h2 {
    padding: 1rem 1rem 0;
    color: #333;
    letter-spacing: 1.5px;
    font-size: 30px;
    font-weight: 800;
    font-family: "Anton", sans-serif;
    text-shadow: 5px 5px 18px rgba(149, 150, 150, 1);
  }
  h3 {
    color: #333;
    font-family: Verdana, sans-serif, serif, sans-serif;
    letter-spacing: 1.5px;
    font-size: 17px;

    font-weight: 500;
    text-shadow: 5px 5px 18px rgba(149, 150, 150, 1);
  }
}

@media screen and (max-width: 800px) {
  .song {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 55vh;

    .song-img {
      width: 12rem;
      height: 12rem;
    }

    img {
      width: 12rem;
      height: 12rem;
      object-fit: cover;
      border-radius: 50%;
    }
  }
}
