.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100vh;
  background: rgb(222, 236, 235);
  box-shadow: 1px 1px 20px gray;
  display: flex;
  flex-direction: column;
  align-items: center;

  overflow: scroll;

  // show and hide library
  transform: translateX(-100%);
  transition: all 0.5s ease;
  opacity: 1;
  .library-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    align-items: center;
    .library-dashboard {
      padding: 1rem;
      color: rgb(24, 39, 48);
    }
    .library-closeIcon {
      flex: 2;
      margin-left: 2rem;
      position: relative;
      left: 10px;
      background-color: darkgray;
      width: 40px;
      height: 40px;
      padding: 10px;
      border-radius: 50%;
      cursor: pointer;
      &:hover {
        background-color: gray;
      }
    }
  }
}

.library-song {
  display: flex;
  padding: 0.5rem;
  align-items: center;
  margin: 1rem 0;
  cursor: pointer;

  &:hover {
    background: rgb(163, 194, 221);
    transition: all 0.2s ease;
  }

  img {
    width: 7rem;
    height: 7rem;
    object-fit: cover;
    border-radius: 70% 30% 30% 70% / 60% 40% 60% 40%;
  }
  .song-infos {
    padding: 1rem;
    color: rgb(24, 39, 48);

    h2 {
      font-family: "Libre Baskerville", serif;
      font-size: 22px;
      font-weight: 900;
    }
    h3 {
      font-family: "Libre Baskerville", serif;
      font-size: 14px;
      font-weight: 400;
    }
  }
}
.active-library {
  transform: translateX(0%);
  opacity: 1;
}

.selected {
  background-color: darkcyan;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #a29a9a #ffffff;
  border-radius: 17px;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 13px;
}

*::-webkit-scrollbar-track {
  background: #ffffff;
}

*::-webkit-scrollbar-thumb {
  background-color: #a29a9a;
  border-radius: 17px;
  border: 3px solid #ffffff;
}

@media screen and (max-width: 800px) {
  .library {
    width: 100%;
    opacity: 1;
  }
}
