.nav {
  margin: auto;
  height: 5%;
  width: 50%;
  display: flex;
  margin-top: 5px;
  //    background:red;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  div {
    font-size: 2em;
    font-weight: 800;
    font-family: "Anton", sans-serif;
    text-shadow: 5px 5px 18px rgba(149, 150, 150, 1);
    color: #393e56;
  }
  button {
    border-radius: 3px;
    padding: 0.5rem;
    font-weight: 800;
    font-size: 20px;
    font-family: "Anton", sans-serif;
    border: 1px solid black;
    color: #393e56;
    cursor: pointer;
    &:hover {
      background-color: gray;
    }
    .music-icon {
      margin-left: 5px;
    }
  }
}
@media screen and (max-width: 800px) {
  .nav {
    display: flex;
    width: 100%;
    div {
      font-size: 1.4em;
    }
    button {
      font-size: 16px;
      display: flex;
    }
  }
}
