.play {
  display: flex;
  flex-direction: column;
  height: 30vh;

  justify-content: center;
  align-items: center;
  .time-control {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 55%;
    color: #333;

    p {
      font-size: 1.5rem;
      padding: 1em;
    }
    input {
      width: 600px;
      height: 3em;
      padding: 1em 0;
    }
    svg {
    }
  }
  .play-control {
    display: flex;

    width: 40%;
    padding: 2rem;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
  }
  input[type="range"]:focus {
    outline: none;
  }
}
@media screen and (max-width: 800px) {
  .play {
    width: 100%;
  }
  .play-control {
    width: 100% !important;
  }
}
